import React from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';

import DropdownNotification from './dropdown/notification.jsx';
import DropdownProfile from './dropdown/profile.jsx';
import SearchForm from './search/form.jsx';

import { useState } from 'react';

const Header = () => {
  const [collapseMegaMenu, setCollapseMegaMenu] = useState(false);

  const isAuth = useSelector((state) => !!state.auth.token);

  const toggleMegaMenu = () => {
    setCollapseMegaMenu(!collapseMegaMenu);
  };

  return (
    <div id='header' className='header navbar-default'>
      <div className='navbar-header'>
        <div className='navbar-brand'>
          <span className='navbar-logo'></span> <b>Market</b> Profit
        </div>

        <button
          type='button'
          className='navbar-toggle pt-0 pb-0 mr-0'
          onClick={toggleMegaMenu}
        >
          <span className='fa-stack fa-lg text-inverse'>
            <i className='far fa-square fa-stack-2x'></i>
            <i className='fa fa-cog fa-stack-1x'></i>
          </span>
        </button>

        <button type='button' className='navbar-toggle'>
          <span className='icon-bar'></span>
          <span className='icon-bar'></span>
          <span className='icon-bar'></span>
        </button>
      </div>

      <ul className='navbar-nav navbar-right'>
        <SearchForm />
        <DropdownNotification />

        {isAuth ? (
          <DropdownProfile />
        ) : (
          <div>
            <Link to='/login'>
              <button className='btn btn-success btn-lg m-1'>Login</button>
            </Link>
            <Link to='/register'>
              <button className='btn btn-success btn-lg m-1'>Register</button>
            </Link>
          </div>
        )}
      </ul>
    </div>
  );
};

export default Header;
