import React, { useState } from 'react';
import { Route, Link } from 'react-router-dom';
import { PageSettings } from './../../config/page-settings.js';

const SidebarNavList = (props) => {
  const [active, setActive] = useState(-1);
  const [clicked, setClicked] = useState(-1);

  const handleExpand = (e, i, match) => {
    e.preventDefault();
    setActive(active === i ? -1 : i);
    setClicked(1);
  };

  const icon = props.data.icon && <i className={props.data.icon}></i>;
  const img = props.data.img && (
    <div className='icon-img'>
      <img src={props.data.img} alt='' />
    </div>
  );
  const caret = props.data.children && !props.data.badge && (
    <b className='caret'></b>
  );
  const label = props.data.label && (
    <span className='label label-theme m-l-5'>{props.data.label}</span>
  );
  const badge = props.data.badge && (
    <span className='badge pull-right'>{props.data.badge}</span>
  );
  const title = props.data.title && (
    <span>
      {props.data.title} {label}
    </span>
  );
  const highlight = props.data.highlight && (
    <i className='fa fa-paper-plane text-theme'></i>
  );

  return (
    <Route
      path={props.data.path}
      exact={props.data.exact}
      children={({ match }) => (
        <li
          className={
            (match ? 'active ' : '') +
            (active || (clicked === -1 && match) || props.data.search
              ? 'expand '
              : 'closed ') +
            (props.data.children ? 'has-sub ' : '')
          }
        >
          {props.data.children ? (
            <Link
              to={props.data.path}
              //   onMouseOver={(e) => handleSidebarOnMouseOver(e, props.data)}
              //   onMouseOut={(e) => handleSidebarOnMouseOut(e, props.data)}
              onClick={props.expand}
            >
              {caret} {badge} {img} {icon} {title} {highlight}
            </Link>
          ) : (
            <Link to={props.data.path}>
              {caret} {img} {icon} {badge} {title} {highlight}
            </Link>
          )}
          {props.data.children && (
            <ul
              className={
                'sub-menu ' +
                (active || (clicked === -1 && match) || props.data.search)
              }
            >
              {props.data.children &&
                props.data.children.map((submenu, i) => (
                  <SidebarNavList
                    data={submenu}
                    key={i}
                    expand={(e) => handleExpand(e, i, match)}
                    active={i === active}
                    clicked={clicked}
                  />
                ))}
            </ul>
          )}
        </li>
      )}
    />
  );
};

export default SidebarNavList;
