import { SIGNIN } from '../actions/auth';

const initialState = {
  token: null,
  isLogin: false,
  id: null,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case SIGNIN:
      return {
        ...state,
        isLogin: action.success,
        id: action.id,
      };
    //   case SET_DID_TRY_AL:
    //     return {
    //       ...state,
    //       didTryAutoLogin: true
    //     };
    //   case LOGOUT:
    //     return {
    //       ...initialState,
    //       didTryAutoLogin: true
    //     };
    // case SIGNUP:
    //   return {
    //     token: action.token,
    //     userId: action.userId
    //   };
    default:
      return state;
  }
};
